@import url(https://fonts.googleapis.com/css2?family=Kumbh+Sans:wght@400;700&display=swap);
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
    box-sizing: border-box;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
:root{
    /* Colors */
    --color-background-gradient-top: hsla(273, 75%, 66%, 1);
    --color-background-gradient-bottom: hsla(240, 73%, 65%, 1);
    --color-text-accordion-active: hsla(238, 29%, 16%, 1);
    --color-text-accordion-hover: hsla(14, 88%, 65%, 1);
    --color-text-accordion: hsla(237, 12%, 33%, 1);
    --color-text-paragraph: hsla(240, 6%, 50%, 1);  
    --color-dividers: hsla(240, 5%, 91%, 1);

    /* Font Family */
    --font-family-main: 'Kumbh Sans', sans-serif;

    /* Fonts Sizes */
    font-size: 12px;
    --font-size-h1-mobile: 3em;
    --font-size-text-mobile: 1em;

    /* Font Weight */
    --font-weight-regular: 400;
    --font-weight-bold: 700;
}


/* Box sizing rules */
*,
*::before,
*::after {
    box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
h5,
h6,
nav {
    margin: 0;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

html {
    overflow-x: hidden;
}

body {
    font-family: var(--font-family-main);
    font-size: 100%;
    line-height: 1.5;
    min-height: 100vh;
    overflow-x: hidden;
    text-rendering: optimizeSpeed;
}

/* Make images easier to work with */
img {
    display: block;
    max-width: 100%;
}

/* Inherit fonts for inputs and buttons */
button,
input,
textarea,
select {
    font: inherit;
}


.main {
    -webkit-align-items: center;
            align-items: center;
    background-image:   linear-gradient(to bottom, 
                            var(--color-background-gradient-top), 
                            var(--color-background-gradient-bottom)
                        );
    display: -webkit-flex;
    display: flex;
    font-family: var(--font-family-main);
    -webkit-justify-content: center;
            justify-content: center;
    min-height: 100vh;
    padding: 0 2em;
    padding-bottom: 6em;
}

