.accordion {
    -webkit-align-items: center;
            align-items: center;
    background-color: hsla(0, 0%, 100%, 1);
    border-radius: 24px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    padding: 0 2em;
    margin-top: 12em;
    width: 100%;
    z-index: 0;
}

.accordion__ilustration {
    -webkit-align-items: center;
            align-items: center;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    width: 100%;
}

.accordion__woman-online {
    background-image: url('https://res.cloudinary.com/dmfhnhyyj/image/upload/v1620956507/illustration-woman-online-desktop_wg5aql.svg');
    background-repeat: no-repeat;
    background-position: center bottom;
    background-size: cover;
    height: 16em;
    margin-top: -9em;
    width: 90%;
    z-index: 10;
}

.accordion__ilustration-pattern{
    background-image: url('https://res.cloudinary.com/dmfhnhyyj/image/upload/v1620956506/bg-pattern-mobile_ssjsdl.svg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center bottom;
    height: 7.5em;
    margin-top: -5.5em;
    width: 90%;
    z-index: 5;
}

.accordion__h1 {
    color: var(--color-text-accordion-active);
    font-size: var(--font-size-h1-mobile);
    font-weight: var(--font-weight-bold);
    margin-top: 0.5em;
    text-align: center;
}


.accordion__section {
    padding-top: 1em;
    padding-bottom: 3em;
    width: 100%;
}

.accordion__header{
    -webkit-align-items: center;
            align-items: center;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    padding-top: 1.2em;
    padding-bottom: 1.2em;
    border-bottom: 1px solid var(--color-dividers);
}


.accordion__header h2 {
    color: var(--color-text-accordion);
}

.active {
    color: var(--color-text-accordion-active);
    font-weight: var(--font-weight-bold);
    transition: all 0.5s;
}
.arrow {
    background-image: url('https://res.cloudinary.com/dmfhnhyyj/image/upload/v1620956507/icon-arrow-down_ti9wx0.svg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    height: .6em;
    width: .8em;
    transition: all 0.5s;
}

.rotate {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
    transition: all 0.5s;
}

.accordion__content {
    color: var(--color-text-paragraph);
    width: 90%;
}


